import "./App.css";
import Navbar from "./components/Navbar";
import News from "./components/News";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
function App() {
  let apiKey = process.env.REACT_APP_NEWS_API
  return (
    <>
      <Router>
        <Navbar title="NewsTemp" />
        <div className="container my-3">
          <Switch>
            <Route exact path="/">
              <News
                key="general"
                country="in"
                category="general"
                pagesize="6"
                apiKey={apiKey}
              />
            </Route>
            <Route exact path="/business">
              <News
                key="business"
                country="in"
                category="business"
                pagesize="6"
                apiKey={apiKey}
              />
            </Route>
            <Route exact path="/entertainment">
              <News
                key="entertainment"
                country="in"
                category="entertainment"
                pagesize="6"
                apiKey={apiKey}
              />
            </Route>
            <Route exact path="/health">
              <News key="health" country="in" category="health" pagesize="6" />
            </Route>
            <Route exact path="/science">
              <News
                key="science"
                country="in"
                category="science"
                pagesize="6"
                apiKey={apiKey}
              />
            </Route>
            <Route exact path="/sports">
              <News key="sports" country="in" category="sports" pagesize="6" />
            </Route>
            <Route exact path="/technology">
              <News
                key="technology"
                country="in"
                category="technology"
                pagesize="6"
                apiKey={apiKey}
              />
            </Route>
          </Switch>
        </div>
      </Router>
    </>
  );
}

export default App;
