/* eslint-disable react-hooks/exhaustive-deps */
import React, { useState, useEffect } from "react";
import NewsItem from "./NewsItem";
import Spin from "./Spin";
import InfiniteScroll from "react-infinite-scroll-component";

export default function News(props) {
  
  let updateNews = async (pageNo) => {
    let url = `https://newsapi.org/v2/top-headlines?country=${props.country}&category=${props.category}&apiKey=${props.apiKey}&page=${pageNo}&pagesize=${props.pagesize}`;
    setLoading(true);
    let response = await fetch(url);
    let newsArticle = await response.json();
    setLoading(false);
    setArticle(newsArticle.articles);
    setTotalResult(newsArticle.totalResults);
  };

  useEffect(async () => {
    updateNews(page);
  }, []);
  
  // let handlePrev = async () => {
  //   setPage(page - 1);
  //   setArticle([]);
  //   updateNews(page - 1);
  // };
  
  // let handleNext = async () => {
  //   setPage(page + 1);
  //   setArticle([]);
  //   updateNews(page + 1);
  // };

  let fetchMoreData = async()=>{
    let url = `https://newsapi.org/v2/top-headlines?country=${props.country}&category=${props.category}&apiKey=${props.apiKey}&page=${page+1}&pagesize=${props.pagesize}`;
    setPage(page+1);
    setLoading(false);
    let response = await fetch(url);
    let newsArticle = await response.json();
    setLoading(false);
    setArticle(article.concat(newsArticle.articles));
    setTotalResult(newsArticle.totalResults);
  }

  let capitalizeFirstLetter = (string)=> {
    return string.charAt(0).toUpperCase() + string.slice(1);
  }
  
  const [article, setArticle] = useState([]);
  const [page, setPage] = useState(1);
  const [loading, setLoading] = useState(false);
  const [totalResult, setTotalResult] = useState(0);
  
  return (
    <>
      <div className="text-center">
        <h2 style={{marginTop:"4rem"}}>Top Headlines - {capitalizeFirstLetter(props.category)}</h2>
      </div>
      <InfiniteScroll
          dataLength={article.length}
          next={fetchMoreData}
          hasMore={article.length!==totalResult}
          loader={<Spin/>}
        >
      {loading && <Spin />}
      <div className="container">
      <div className="row">
        {article.map((element) => {
          return (
            <div className="col md-4" key={element.url}>
              <NewsItem
                title={element.title.slice(0, 50)}
                desc={
                  element.description
                    ? element.description.slice(0, 100)
                    : "No further Information Provided"
                }
                imageUrl={
                  element.urlToImage
                    ? element.urlToImage
                    : "https://images.moneycontrol.com/static-mcnews/2021/08/shutterstock_1493393651-770x433.jpg"
                }
                url={element.url}
                source={element.source.name}
                author={element.author}
                dt={element.publishedAt}
              />
            </div>
          );
        })}
          </div>
          </div>
        </InfiniteScroll>
      {/* <div className="container">
        <div className="d-flex justify-content-between">
          <button
            disabled={page <= 1}
            type="button"
            className="btn btn-dark"
            onClick={handlePrev}
          >
            &larr; Previous
          </button>
          <button
            disabled={page > totalResult / props.pagesize}
            type="button"
            className="btn btn-dark"
            onClick={handleNext}
          >
            Next &rarr;
          </button>
        </div>
      </div> */}
    </>
  );
}
