import React from "react";
export default function NewsItem(props) {
  return (
    <>
      <div className="card my-3 mx-3" style={{ width: "17rem" }}>
      <div style={{display:"flex",justifyContent:"flex-end",position:'absolute',left:"0"}}>
      <span className="badge rounded-pill bg-danger">{props.source}</span>
      </div>
        <img
          src={props.imageUrl}
          className="card-img-top"
          alt="..."
          height="200rem"
        />
        <div className="card-body">
          <h5 className="card-title">{props.title}...</h5>
          <p className="card-text">{props.desc}...</p>
          <p className="card-text"><small className="text-muted">By {props.author?props.author:"Unknown"} on {props.dt}</small></p>
          <a
            href={props.url}
            className="btn tbn-sm btn-dark"
            target="_blank"
            rel="noreferrer"
          >
            Read More
          </a>
        </div>
      </div>
    </>
  );
}
